h1{
    font-size: 4.5em;
}
h2{
    font-size: 3.5em;
}
h3{
    font-size: 3em;
}
p{
    font-size: 1.25em;
    line-height: 1.5;
}
.pitch-button{
    background-color: red;
    background-image: linear-gradient(to top right, red, yellow);
    color: white;
    border: 0px;
    border-radius: 6px;
    width: 25rem;
    height: auto;
    margin-right: 7rem;
    padding: .75rem 3rem .75rem 3rem;
    filter: drop-shadow(0 0 0.5rem rgb(255, 165, 0, 0.5));
    font-family: 'Roboto', sans-serif;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}
.pitch{
    background-color: #ffe866;
    font-family: 'Acme', sans-serif;
    padding: 5rem 7rem 5rem 5rem;
    color: black;
}
.perks{
    background-color: white;
    font-family: 'Acme', sans-serif;
    padding: 5rem 7rem 5rem 5rem;
    color: black;
}
#tri-fold{
    padding: 0 .1rem 0 .1rem;
    font-family: 'Acme', sans-serif;
    height: auto;
    width: 100%;
}
#tri-fold-label-1{
    padding: 1.5rem 0 1.5rem 0;
    color: white;
    background-color: red;
    background-image: linear-gradient(to top, red, #ff9999);
}
#tri-fold-label-2{
    background-color: black;
    padding: 1.5rem 0 1.5rem 0;
    color: white;
    background-image: linear-gradient(to top, black, #666666);
}
#tri-fold-label-3{
    background-color: #ffe866;
    background-image: linear-gradient(to top, #ffe866, #fffbe6);
    color: white;
    padding: 1.5rem 0 1.5rem 0;
}
.tri-fold-bottom{
    color: black;
    padding: 2rem 3rem 2rem 3rem;
}
.gears{
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 15rem;
    font-family: 'Acme', sans-serif;   
}
.step{
    text-align: center;
    color: white;
    background-color: black;
    border: 0 solid;
    border-radius: 6px;
    margin: 3rem;
    width: 30rem;
    padding: 3rem 0 3rem 0;
}
#destination-container{
    background-color: lightgrey;
    width: 100vw;
    padding: 5rem;
}
#destination-card{
    width: 22rem;
    margin: 1.4rem 0 1.4rem 0;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    text-align: center;
    font-family: 'Acme', sans-serif;  
}
.final-pitch{
    background-color: red;
    background-image: linear-gradient(to top right, red, yellow);
    color: white;
    border: 0px;
    border-radius: 6px;
    width: 35rem;
    height: auto;
    margin-bottom: 3rem;
    padding: .75rem 3rem .75rem 3rem;
    filter: drop-shadow(0 0 0.5rem rgb(255, 165, 0, 0.5));
    font-family: 'Roboto', sans-serif;
    position: relative;
    left: 50%;
    transform: translate(-50%);
}